export default {
  computed: {
    roles: function roles() {
      return this.$store.getters.roles;
    },
    switchRoles: {
      get: function get() {
        return this.roles[0];
      },
      set: function set(val) {
        var _this = this;

        this.$store.dispatch('ChangeRoles', val).then(function () {
          _this.$emit('change');
        });
      }
    }
  }
};